.gallery{
    padding: auto 30rem;
}
.galeria-imagenes{
    padding: 0 0 10rem 0;
    list-style: none;

    @include grid (1, 1rem);
    grid-auto-flow: dense;

    @include tablet {
        @include grid (2, $separacion/2);
    }

    @include desktop {
        @include grid (4, $separacion/2);
    }
}

.imagen {
    overflow:hidden;
    position: relative;
}
.elemento-imagen{
    object-fit: cover;
    object-position: center center;
    height: 40rem;
}

.selected img{
    filter: grayscale(1);
    position: relative;
    transition: all 1s ease;
}
.selected:after{
    position: absolute;
    content: '';
    background-image: url('/build/img/cheque.svg');
    background-position: center center;
    background-size:contain;
    background-repeat: no-repeat;
    height: 5rem;
    top: 1rem;
    bottom: 1rem;
    left: 1rem;
    right: 1rem;
    pointer-events: none;
    transition: all .5s ease;
}

.back{
    display: flex;
    justify-content: center;
    margin-top: 3rem;
}
.parrafoImg{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $blanco;
    opacity: 0.8;
    pointer-events: none;
    display: none;
    transition: all .5s ease;
}
.parrafoImg p{
    margin: 1rem 0;
    text-align: center;
    font-weight: $bold;
}