.bg-galerias{
    background-image: url('/build/img/bg-menu.webp');
    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 3rem;
}
.logo-pagina{
    max-width: 45rem;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.galeria-categorias{
    padding: 0 0 10rem 0;

    @include grid (1, $separacion);

    @include tablet {
        @include grid (2, $separacion/2);
    }

    @include desktop {
        @include grid (4, $separacion/2);
    }
}

.cont-categoria {
    overflow:hidden;
    position: relative;
}
.nombre-categoria{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $blanco;
    opacity: 0.8;
    pointer-events: none;
}
.nombre-categoria p{
    margin: 1rem 0;
    text-align: center;
    font-weight: $bold;
}
.nav{
    margin-bottom: 2rem;
}
.slider{
    overflow: hidden;
    position: relative;
    height: 30rem;
}
@include desktop{
    .slider{
        height: 35rem;
    }
}
.slider ul{
    display: flex;
    padding: 0;
    width: 400%;
    height: 100%;
    animation: change 20s infinite;
    animation-direction: alternate;
    overflow:hidden;
    position: relative;
}
.slider li{
    width: 100%;
    height: 100%;
    list-style: none;
    position: relative;
}
.slider img{
    width: 100%;
    height: 100%;
}

@keyframes change {
    0% {margin-left: 0;}
    20% {margin-left: 0;}

    25% {margin-left: -100%;}
    45% {margin-left: -100%;}

    50% {margin-left: -200%;}
    70% {margin-left: -200%;}

    75% {margin-left: -300%;}
    100% {margin-left: -300%;}
}
.img-categoria{
    object-fit: cover;
    object-position: center center;
    height: 100%;
}